import {Route, Routes, useLocation} from "react-router-dom";
import React, {useEffect, useState} from "react";
import Menubar from "./components/Menubar";
import Main from "./pages/Main";
import Contests from "./pages/Contests";
import Info from "./pages/Info";
import Archive from "./pages/Archive";
import Submissions from "./pages/Submissions";
import Problems from "./pages/Problems";
import Submission from "./pages/Submission";
import Profile from "./pages/Profile/Profile";
import ProfileMain from "./pages/Profile/ProfileMain";
import ProfileSubmissions from "./pages/Profile/ProfileSubmissions";
import ProfileSettings from "./pages/Profile/ProfileSettings";
import Problem from "./pages/Problem/Problem";
import ProblemStatement from "./pages/Problem/ProblemStatement";
import ProblemSubmit from "./pages/Problem/ProblemSubmit";
import ProblemSubmissions from "./pages/Problem/ProblemSubmissions";
import ProblemRanklist from "./pages/Problem/ProblemRanklist";
import Login from "./pages/Auth/Login";
import Register from "./pages/Auth/Register";
import NotFound from "./pages/Error/NotFound";
import PageLoadingAnimation from "./components/PageLoadingAnimation";
import {findRouteIndex} from "./util/RouteUtil";
import {updatePageData} from "./util/UpdatePageData";
import { CSSTransition, TransitionGroup } from "react-transition-group"
import {routeMap} from "./config/RouteConfig";

const routesToFetch = [
    routeMap.main,
    routeMap.contests,
    routeMap.info,
    routeMap.archive,
    routeMap.submissions,
    routeMap.problems,
    routeMap.submission,
    routeMap.login,
    routeMap.register
]

function RoutingComponent() {
    const location = useLocation()
    const [data, setData] = useState(null)
    const [loadingCount, setLoadingCount] = useState(0)

    useEffect(() => {
        let isMounted = true
        updatePageData(location, routesToFetch, setData, setLoadingCount, () => isMounted)

        return () => {
            isMounted = false
        }
    }, [location]);

    let loadingAnimation =  <PageLoadingAnimation />
    let pageContent = null

    if (loadingCount === 0 && data) {
        loadingAnimation = null
        pageContent =
            <Routes>
                <Route path={routeMap.main} element={<Main data={data} />}  />
                <Route path={routeMap.contests} element={<Contests data={data} />} />
                <Route path={routeMap.info} element={<Info data={data} />} />
                <Route path={routeMap.archive} element={<Archive data={data} />} />
                <Route path={routeMap.submissions} element={<Submissions data={data} />} />
                <Route path={routeMap.problems} element={<Problems data={data} />} />
                <Route path={routeMap.submission} element={<Submission data={data} />} />
                <Route path={routeMap.login} element={<Login data={data} />} />
                <Route path={routeMap.register} element={<Register data={data} />} />
                <Route path={routeMap.profile} element={<Profile />} >
                    <Route path={routeMap.profile} element={<ProfileMain />} />
                    <Route path={routeMap.profileSubmissions} element={<ProfileSubmissions />} />
                    <Route path={routeMap.profileSettings} element={<ProfileSettings />} />
                </Route>
                <Route path={routeMap.problem} element={<Problem />} >
                    <Route path={routeMap.problem} element={<ProblemStatement />} />
                    <Route path={routeMap.problemSubmit} element={<ProblemSubmit />} />
                    <Route path={routeMap.problemSubmissions} element={<ProblemSubmissions />} />
                    <Route path={routeMap.problemRanklist} element={<ProblemRanklist />} />
                </Route>
                <Route path="*" element={<NotFound />} />
            </Routes>
    }
    return (
        <>
            <div className="pb-20">
                <Menubar />
            </div>
            <div className={`transition-opacity ease-linear duration-250 ${loadingCount === 0? "opacity-0" : "opacity-100"}`}>
                {loadingAnimation}
            </div>
            <div className={`transition-opacity ease-linear duration-250 ${loadingCount === 0? "opacity-100" : "opacity-0"}`}>
                {pageContent}
            </div>
        </>
    )
}

export default RoutingComponent