import React, { useState } from 'react';
import TextBox from './TextBox';
import TextBoxDropdown from './TextBoxDropdown';
import TagDropdown from './TagDropdown';
import { useLocation } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import DropdownFrame from "./DropdownFrame";
import queryString from "query-string";

function ProblemFilter() {
    const [title, setTitle] = useState("");
    const [tags, setTags] = useState([]);
    const [category, setCategory] = useState([-1, ""]);
    const location = useLocation();
    const navigate = useNavigate();

    const handleTitleChange = (newText) => {
        setTitle(newText);
    };
    const handleCategoryChange = (selected, newText) => {
        setCategory([selected, newText]);
    };
    const handleTagsChange = (tags) => {
        setTags(tags);
    };
    const handleSubmit = () => {
        const qString = queryString.stringify({
            title: title,
            tags: tags.join(","),
            category: category
        })
        navigate(`${location.pathname}?${qString}`);
    };
    return (
        <div className="w-full">
            <div className="mb-4">
                <TextBox id="filterTitle" label="Feladatcm" initText={title} onChange={handleTitleChange} />
            </div>
            <div className="mb-4">
                <TagDropdown id="filterTags" label="Cmkk" fillSelected={false} itemNames={[
                    "matematika",
                    "moh",
                    "dinamikus programozs",
                    "adatszerkezetek",
                ]} initTags={tags} onChange={handleTagsChange} />
            </div>
            <div className="mb-5">
                <TextBoxDropdown id="filterCategory" label="Kategria" initText={category[1]} initSelected={category[0]} fillSelected={true} itemNames={[
                    "IOI-CEOI Vlogat 2023",
                    "IOI-CEOI Vlogat 2023  1. fordul",
                    "IOI-CEOI Vlogat 2023  2. fordul",
                    "IOI-CEOI Vlogat 2023  3. fordul"
                ]} onChange={handleCategoryChange} />
            </div>
            <div className="flex justify-center">
                <button className="mr-1 btn-indigo w-32" onClick={handleSubmit}>Keres</button>
                <button className="ml-1 btn-gray w-32">Visszallt</button>
            </div>
        </div>
    )
}

export function ProblemFilterFrame() {
    return (
        <DropdownFrame>
            <div className="px-8 py-6 border-t border-default">
                <ProblemFilter />
            </div>
        </DropdownFrame>
    )
}

export default ProblemFilter;